body {
  background: #212121;
}

p {
  margin: 0;
  padding: 0;
}

button {
  background-color: #0000;
  border: none;
  outline: none;
}

.message {
  -webkit-backdrop-filter: blur(21px);
  backdrop-filter: blur(21px);
  color: #eee;
  text-align: left;
  background: linear-gradient(-45deg, #fff3 0%, #fff0 100%);
  border-radius: 16px;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  height: 50px;
  padding: 15.5px;
  font-family: Montserrat;
  transition-duration: .35s;
  transition-delay: 75ms;
  transition-timing-function: ease-in-out;
  display: flex;
  position: absolute;
  left: 50%;
  right: 50%;
  transform: translate(-50%, -150%);
  box-shadow: 0 0 2px #ffffff91;
}

.message__close {
  color: #cbcbcb;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  transition-duration: .25s;
  transition-delay: 50ms;
  display: flex;
}

.message__close:hover {
  background-color: #8d8d8d86;
}

.shown-msg {
  transition-duration: .35s;
  transition-delay: 75ms;
  transition-timing-function: ease-in-out;
  transform: translate(-50%, 50%);
}

form {
  color: #9ca3af;
  background-color: #111827;
  border-radius: .75rem;
  width: 320px;
  padding: 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form__title {
  text-align: center;
  color: #f3f4f6;
  margin: 0 0 1.5rem;
  padding: 0;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 2rem;
}

form label {
  margin-bottom: 5px;
  font-size: 1rem;
  line-height: 1.25rem;
  display: block;
}

form input {
  color: #f3f4f6;
  background-color: #111827;
  border: 1px solid #374151;
  border-radius: .375rem;
  outline: 0;
  width: 100%;
  margin-bottom: .35rem;
  padding: .75rem 1rem;
}

input:focus {
  border-color: #a78bfa;
}

form textarea {
  resize: none;
  color: #f3f4f6;
  background-color: #111827;
  border: 1px solid #374151;
  border-radius: .375rem;
  outline: 0;
  width: 100%;
  margin-bottom: .35rem;
  padding: .75rem 1rem;
}

form input:last-child {
  margin-bottom: 0;
}

form button {
  text-align: center;
  color: #111827;
  background-color: #a78bfa;
  border: none;
  border-radius: .375rem;
  width: 100%;
  margin-top: 10px;
  padding: .75rem;
  font-weight: 600;
  display: block;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  transform: translateY(100%);
}

